define("appkit/models/calculator",
  [],
  function() {
    "use strict";
    var Calculator = Ember.Object.extend({
      firstOperand: null,
      secondOperand: null,
      operation: null,
      result: null,
      status: null,

      display: function() {
        return this.get('result')
      }.property('result')
    });

    return Calculator;
  });